@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
html,
body {
	background-color: rgb(38, 43, 43);
}
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.App {
	font-family: 'Open Sans', sans-serif;
	margin: 2rem;
	background-color: rgb(250, 255, 254);
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	min-height: 100vh;
}

@media screen and (max-width: 500px) {
	.App {
		flex-direction: column;
		align-items: center;
	}
}

header {
	flex-basis: 30%;
	width: 25%;
}

main {
	flex-basis: 70%;
	width: 75%;
}

.navLink {
	padding: 1rem;
	margin-top: 1rem;
	background-color: rgb(235, 255, 254);
}

.navLink:hover {
	background-color: rgb(235, 240, 254);
}

ul {
	list-style-type: none;
	margin-top: 4rem;
}

a {
	text-decoration: none;
}

@media screen and (max-width: 500px) {
	ul {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		list-style-type: none;
		margin-left: -10rem;
		margin-top: 0rem;
	}
	.navLink {
		margin-left: 3rem;
		width: 100%;
		text-align: center;
	}
}

.title {
	width: 50%;
	text-align: center;
}

.logo {
	color: rgb(0, 128, 255);
}

@media screen and (max-width: 500px) {
	.title {
		width: 100%;
	}
}

.UserCardContainer {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	padding: 1rem;
	margin: 1rem;
	background-color: rgb(235, 255, 254);
	border-radius: 20px;
}

.UserCard {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-left: 2rem;
}

@media screen and (max-width: 500px) {
	.UserCardContainer {
		flex-direction: column;
	}
}

p {
	margin: 0.2rem;
}

.userImage {
	border-radius: 50%;
}

.title {
	width: 50%;
	text-align: center;
}

.logo {
	color: rgb(0, 128, 255);
}

table {
	padding: 1rem;
}

thead {
	background-color: rgb(235, 255, 254);
	height: 4rem;
}
thead > tr > th {
	font-weight: bold;
	border-bottom: 2px solid black;
}

tbody > tr > th {
	font-weight: normal;
	border-bottom: 1px solid black;
	padding: 1rem;
}

tbody > tr:nth-child(2n) {
	background-color: rgb(235, 255, 254);
}

tbody > tr:nth-child(2n + 1) {
	background-color: rgb(235, 240, 254);
}

@media screen and (max-width: 500px) {
	.title {
		width: 100%;
	}
	tbody > tr > th {
		padding: 0;
	}
}

.userDetailsContainer {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	min-height: 100vh;
	margin: 4rem 1rem 0 1rem;

	background-color: rgb(235, 255, 254);
}
.userMoreInfo {
	display: flex;
	width: 100%;
	margin-top: 2rem;
	flex-direction: row;
	justify-content: space-evenly;
	align-items: flex-start;
}

@media screen and (max-width: 500px) {
	.userMoreInfo {
		width: 100%;
		flex-direction: column;
		margin-left: 2rem;
	}
	.userMoreInfo > div {
		padding: 0.5rem;
	}
}

.logo {
	color: rgb(0, 128, 255);
}

.home {
	display: flex;
	margin-top: 20%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.loadingSpinner {
	width: 100%;
	margin: 50% auto;
	border: 16px solid #f3f3f3;
	border-radius: 50%;
	border-top: 16px solid #3498db;
	width: 60px;
	height: 60px; /* Safari */
	animation: spin 2s linear infinite;
}

